///fonts///
$font-primary: "Nunito Sans", sans-serif;
$font-secondary: "Next-ART";
$font-size-base: 16px;
 
///colors///
$primary-color: #1A3E3E;
$bg-color: #F3F5F4;
$text-accent-color: #F2BE22;
$bg-accent-color: #ffffff;
$brand-light-color: #FDFDFD;
$brand-dark-color: #18121E;


///font///
$h1-font-size: 50px;
$h2-font-size: 32px;
$h3-font-size: 26px;
$h4-font-size: 24px;
$h5-font-size: 20px;
$h6-font-size: 18px;

$h1-line-height: 1.5;
$h2-line-height: 1.2;
$h3-line-height: 1.2;
$h4-line-height: 1.33;
$h5-line-height: 1.2;
$h6-line-height: 1.11;

@mixin background-blur {
    background-color: #ffffff30;
    backdrop-filter: blur(6px);
}

@mixin btn_secondary {
    border-color: $brand-light-color;
    background-color: $brand-light-color;
    color: $primary-color;
    &:hover {
        background-color: $primary-color;
        color: $brand-light-color;
        border-color: $primary-color;
    } 
}

@mixin section-padding {
    padding: 80px 0 100px;
}